/** Start: overriding theme values */
$primary: #7269ef;
// @import "~@progress/kendo-theme-bootstrap/dist/all.scss";
@import "~@progress/kendo-theme-bootstrap/scss/chat/_index.scss";
@import "~@progress/kendo-theme-bootstrap/scss/popup/_index.scss";
@import "~@progress/kendo-theme-bootstrap/scss/avatar/_index.scss";
@import "~@progress/kendo-theme-bootstrap/scss/loader/_index.scss";

/** End: overriding theme values */

.chat-wrap {
  max-width: 400px;
  position: fixed;
  right: 0;
  height: auto;
  bottom: 10px;
  z-index: 100;
  width: 100%;
}

.chat-con {
  position: relative;
  width: 100%;
  height: auto;
  padding: 0 10px;
}

.chat-widget-layout {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.chat-container,
.chat-loading {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 0px 15px 5px #d7d7d7;
  width: 100%;
  height: 400px;
  position: relative;
  transition: height 0.2s ease-out;
  border-radius: 0.5rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  &.hidden {
    display: none;
    height: 0px;
    width: 0px;
  }

  .k-chat {
    margin: 0;

    .k-message-list {
      &::-webkit-scrollbar {
        width: 7px;
      }

      &::-webkit-scrollbar-track {
        background: #f1f1f1;
      }

      &::-webkit-scrollbar-thumb {
        background: #888;
      }

      &::-webkit-scrollbar-thumb:hover {
        background: #555;
      }
    }

    .k-message-box {
      padding: 0 8px;
    }

    .k-message-group {
      .k-bubble {
        border-color: #66ccff;
        color: white;
        background-color: #66ccff;
      }

      &.k-alt {
        .k-bubble {
          border-color: #10B0C1;
          color: white;
          background-color: #10B0C1;
        }
      }
    }
  }

  .k-quick-replies {
    .k-quick-reply {
      color: #66ccff;
      border-color: #66ccff;

      &:hover {
        background-color: #66ccff;
        border-color: #66ccff;
        color: white;
      }
    }
  }
}

.chat-loading {
  background-color: #f5f7fb;
  align-items: center;
  justify-content: center;

  .img-speech-bubble {
    width: 6rem;
  }

  .title {
    font-size: 1.6rem;
    color: gray;
    margin-top: 1rem;
    margin-bottom: 0.25rem;
  }

  .message {
    font-size: 1.1rem;
    color: gray;
    margin: 0;
    width: 70%;
    text-align: center;
  }
}

.button-container {
  cursor: pointer;
  background-color: #7269ef;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;

  .icon {
    transition: transform 0.3s ease;
    position: absolute;
    width: 35px;
    height: 35px;
  }

  .icon.hidden {
    transform: scale(0);
  }
}

.chat-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 10px 20px;
  background-color: #ffffff;

  .k-avatar {
    width: 48px;
    height: 48px;
    flex-basis: 48px;
  }

  .d-name {
    font-weight: 600;
    font-size: 0.8rem;
  }

  .chat-close {
    font-size: 0.5rem;
  }
}

.emoji-picker-pop {
  background: transparent;

  .emoji-picker-container {
    width: 247px;
  }

  .emoji-mart {
    border: none;
  }
}

.cw-bottom-0 {
  bottom: 0 !important;
}

.chat-wrap {
  .k-input {
    height: 50px;
    font-size: 20px;
    border: none;
    background: inherit;

    &:focus-within {
      border-color: none;
      box-shadow: none;
      background: inherit;
    }
  }

  .k-chat .k-alt .k-bubble {
    background-color: #00acb1 !important;
    border-color: #00acb1 !important;
  }
}